.ui.menu .item:before {
    display: none;
}
.ui.menu {
    z-index: 2;
    background-color: grey;
}
.ui.image{
    max-width: 150px;
}
.content{
    text-align: center;
}
.cart{
    color: #21ba45;
}
.List {
    width:200px !important;
}
.List img{
    width: 200px !important;
}
.purple{
    background-color: #4B0082 !important;
}
.cartBtn{
    border-radius: 10px !important;
    position: absolute;
    width: 90%;
    height: 56%;
    font-size: 13px !important;
    margin-right: 5px !important;
    border-radius: 2px !important;
}
.ui.dropdown{
    margin-left: 5px;
    width: 112% !important;;
    background-color: white !important;
    display: inline-block !important;
    outline: 0 !important;
    text-align: center;
}
.basket{
    padding-right: 50px !important;
    color: white;
}
.basket.icon{
    padding: 0 !important;
    position: absolute !important;
    margin-right: 40px !important;
}
.navPos {
    background-color: #343a40 !important;
    position: fixed;
    width: 100%;
    z-index: 1;
    opacity: 0.9;
}
.container{
    padding-top: 30px !important;
}
.fonts-header {
    font-family: 'Lacquer', sans-serif !important;
    color: white !important;
    font-size: 18px;
}
.fonts-body a{
    font: 400 15px/1.8 Lato, sans-serif !important;
    color: #777 !important;
}
.copyright{ 
    color: rgba(0,0,0,.7); 
    text-shadow: 0 1px rgba(255, 255, 255, 0.1);
    font-size: 15px !important;
    font-weight: 400 !important;
}
.itemDesc{
    margin-top: 10% !important;
    height: 100% !important;
}
@media only screen and (max-width: 600px) {
    .fonts-body a{
        font: 400 13px/1.8 Lato, sans-serif !important;
        color: #777 !important;
    }
    .fonts-header {
        font-size: 18px;
    }
    .ui.menu .item{
        padding: 0.8em 1em;
    }
}
.name{
    width: 60% !important
}
#mobForm{
    width: 60% !important
}
#postId{
    width: 30% !important;
}